import React from 'react'
import { Link } from 'react-router-dom'
import { Button } from 'semantic-ui-react'

const NoLogin = () => (
  <div>
    <p>你还未登陆，请先</p>
    <Button primary Inverted><Link to='/login'>登陆</Link></Button>
  </div>
)
export default NoLogin



